import React from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Grid,
  Button,
  Chip,
  Link
} from '@mui/material';
import { ArrowBack as ArrowBackIcon } from '@mui/icons-material';

// Sample data - In a real application, this would come from an API
const patents = {
  'US20230123456': {
    title: 'Methods and systems for determining page quality',
    date: '2023-01-15',
    category: 'on-page-seo',
    subCategory: 'quality-metrics',
    summary: 'A system for evaluating web page quality based on various on-page factors.',
    fullText: 'This patent describes methods and systems for determining the quality of web pages based on various on-page factors including content relevance, structure, and user engagement metrics.',
    googlePatentUrl: 'https://patents.google.com/patent/US20230123456'
  },
  'US20230123457': {
    title: 'System for analyzing website structure',
    date: '2023-03-10',
    category: 'technical-seo',
    subCategory: 'site-structure',
    summary: 'Methods for analyzing and optimizing website architecture for search engines.',
    fullText: 'This patent outlines systems and methods for analyzing website structure and providing recommendations for optimization based on search engine crawling and indexing patterns.',
    googlePatentUrl: 'https://patents.google.com/patent/US20230123457'
  }
};

// Sample related patents data
const relatedPatents = {
  'US20230123456': [
    {
      id: 'US20230123459',
      title: 'Content relevance determination system',
      category: 'on-page-seo'
    }
  ],
  'US20230123457': [
    {
      id: 'US20230123458',
      title: 'Content hierarchy analysis system',
      category: 'technical-seo'
    }
  ]
};

const PatentDetail = () => {
  const { patentId } = useParams();
  const navigate = useNavigate();
  const patent = patents[patentId];

  if (!patent) {
    return (
      <Box>
        <Button
          startIcon={<ArrowBackIcon />}
          onClick={() => navigate(-1)}
          sx={{ mb: 2 }}
        >
          Go Back
        </Button>
        <Typography variant="h5" component="h1">
          Patent not found
        </Typography>
      </Box>
    );
  }

  return (
    <Box>
      <Button
        startIcon={<ArrowBackIcon />}
        onClick={() => navigate(-1)}
        sx={{ mb: 2 }}
      >
        Go Back
      </Button>

      <Card sx={{ mb: 4 }}>
        <CardContent>
          <Typography variant="h4" component="h1" gutterBottom>
            {patent.title}
          </Typography>
          
          <Grid container spacing={2} sx={{ mb: 2 }}>
            <Grid item>
              <Chip label={`ID: ${patentId}`} variant="outlined" />
            </Grid>
            <Grid item>
              <Chip
                label={patent.category.split('-').map(word => 
                  word.charAt(0).toUpperCase() + word.slice(1)
                ).join(' ')}
                color="primary"
              />
            </Grid>
            <Grid item>
              <Chip
                label={patent.subCategory.split('-').map(word => 
                  word.charAt(0).toUpperCase() + word.slice(1)
                ).join(' ')}
                color="secondary"
              />
            </Grid>
          </Grid>

          <Typography variant="body2" color="text.secondary" gutterBottom>
            Publication Date: {new Date(patent.date).toLocaleDateString()}
          </Typography>

          <Typography variant="h6" gutterBottom sx={{ mt: 3 }}>
            Summary
          </Typography>
          <Typography variant="body1" paragraph>
            {patent.summary}
          </Typography>

          <Typography variant="h6" gutterBottom>
            Full Description
          </Typography>
          <Typography variant="body1" paragraph>
            {patent.fullText}
          </Typography>

          <Link
            href={patent.googlePatentUrl}
            target="_blank"
            rel="noopener noreferrer"
            color="primary"
            sx={{ display: 'block', mt: 2 }}
          >
            View on Google Patents
          </Link>
        </CardContent>
      </Card>

      {relatedPatents[patentId] && relatedPatents[patentId].length > 0 && (
        <Box>
          <Typography variant="h5" gutterBottom sx={{ mb: 2 }}>
            Related Patents
          </Typography>
          <Grid container spacing={3}>
            {relatedPatents[patentId].map((relatedPatent) => (
              <Grid item xs={12} sm={6} md={4} key={relatedPatent.id}>
                <Card>
                  <CardContent>
                    <Typography variant="h6" gutterBottom noWrap>
                      {relatedPatent.title}
                    </Typography>
                    <Typography variant="body2" color="text.secondary" gutterBottom>
                      Patent ID: {relatedPatent.id}
                    </Typography>
                    <Button
                      color="primary"
                      onClick={() => navigate(`/patent/${relatedPatent.id}`)}
                    >
                      View Details
                    </Button>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Box>
      )}
    </Box>
  );
};

export default PatentDetail;