import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  CardActionArea,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField
} from '@mui/material';
import { useNavigate } from 'react-router-dom';

// Sample data - In a real application, this would come from an API
const patents = {
  'on-page-seo': [
    {
      id: 'US20230123456',
      title: 'Methods and systems for determining page quality',
      date: '2023-01-15',
      summary: 'A system for evaluating web page quality based on various on-page factors.',
      subCategory: 'quality-metrics'
    },
    {
      id: 'US20230123459',
      title: 'Content relevance determination system',
      date: '2023-02-20',
      summary: 'Methods for determining content relevance and quality for search engines.',
      subCategory: 'content-analysis'
    }
  ],
  'technical-seo': [
    {
      id: 'US20230123457',
      title: 'System for analyzing website structure',
      date: '2023-03-10',
      summary: 'Methods for analyzing and optimizing website architecture for search engines.',
      subCategory: 'site-structure'
    }
  ]
};

const subCategories = {
  'on-page-seo': ['quality-metrics', 'content-analysis', 'meta-tags'],
  'technical-seo': ['site-structure', 'indexing', 'crawling'],
  'pagination': ['content-pagination', 'infinite-scroll'],
  'heading-usage': ['heading-hierarchy', 'semantic-structure'],
  'linking': ['internal-linking', 'external-linking', 'pagerank'],
  'other': ['miscellaneous']
};

const CategoryPage = () => {
  const { categoryId } = useParams();
  const navigate = useNavigate();
  const [sortBy, setSortBy] = useState('date');
  const [subCategory, setSubCategory] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');

  const categoryName = {
    'on-page-seo': 'On-Page SEO',
    'technical-seo': 'Technical SEO',
    'pagination': 'Pagination',
    'heading-usage': 'Heading Usage',
    'linking': 'Linking Structure',
    'other': 'Other SEO Topics'
  }[categoryId] || 'Unknown Category';

  const filteredPatents = (patents[categoryId] || []).filter(patent => {
    const matchesSubCategory = subCategory === 'all' || patent.subCategory === subCategory;
    const matchesSearch = searchTerm === '' || 
      patent.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      patent.summary.toLowerCase().includes(searchTerm.toLowerCase());
    return matchesSubCategory && matchesSearch;
  });

  const sortedPatents = [...filteredPatents].sort((a, b) => {
    if (sortBy === 'date') {
      return new Date(b.date) - new Date(a.date);
    }
    return a.title.localeCompare(b.title);
  });

  return (
    <Box>
      <Typography variant="h4" component="h1" gutterBottom>
        {categoryName}
      </Typography>

      <Box sx={{ mb: 4 }}>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={4}>
            <FormControl fullWidth>
              <InputLabel>Sort By</InputLabel>
              <Select
                value={sortBy}
                label="Sort By"
                onChange={(e) => setSortBy(e.target.value)}
              >
                <MenuItem value="date">Most Recent</MenuItem>
                <MenuItem value="title">Title</MenuItem>
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12} sm={4}>
            <FormControl fullWidth>
              <InputLabel>Sub Category</InputLabel>
              <Select
                value={subCategory}
                label="Sub Category"
                onChange={(e) => setSubCategory(e.target.value)}
              >
                <MenuItem value="all">All</MenuItem>
                {(subCategories[categoryId] || []).map((sub) => (
                  <MenuItem key={sub} value={sub}>
                    {sub.split('-').map(word => word.charAt(0).toUpperCase() + word.slice(1)).join(' ')}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextField
              fullWidth
              label="Search in this category"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </Grid>
        </Grid>
      </Box>

      <Grid container spacing={3}>
        {sortedPatents.map((patent) => (
          <Grid item xs={12} sm={6} md={4} key={patent.id}>
            <Card>
              <CardActionArea onClick={() => navigate(`/patent/${patent.id}`)}>
                <CardContent>
                  <Typography variant="h6" gutterBottom noWrap>
                    {patent.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" gutterBottom>
                    Patent ID: {patent.id}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" gutterBottom>
                    Date: {new Date(patent.date).toLocaleDateString()}
                  </Typography>
                  <Typography variant="body2">
                    {patent.summary}
                  </Typography>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        ))}
        {sortedPatents.length === 0 && (
          <Grid item xs={12}>
            <Typography variant="body1" color="text.secondary" align="center">
              No patents found matching your criteria.
            </Typography>
          </Grid>
        )}
      </Grid>
    </Box>
  );
};

export default CategoryPage;