import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  IconButton,
  InputBase,
  Box,
  Menu,
  MenuItem,
  alpha
} from '@mui/material';
import {
  Search as SearchIcon,
  Menu as MenuIcon
} from '@mui/icons-material';

const categories = [
  { id: 'on-page-seo', name: 'On-Page SEO' },
  { id: 'technical-seo', name: 'Technical SEO' },
  { id: 'pagination', name: 'Pagination' },
  { id: 'heading-usage', name: 'Heading Usage' },
  { id: 'linking', name: 'Linking Structure' },
  { id: 'other', name: 'Other SEO Topics' }
];

const Navbar = () => {
  const navigate = useNavigate();
  const [searchQuery, setSearchQuery] = useState('');
  const [anchorEl, setAnchorEl] = useState(null);

  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleCategoryClick = (categoryId) => {
    handleMenuClose();
    navigate(`/category/${categoryId}`);
  };

  const handleSearch = (e) => {
    e.preventDefault();
    if (searchQuery.trim()) {
      navigate(`/search?q=${encodeURIComponent(searchQuery.trim())}`);
      setSearchQuery('');
    }
  };

  return (
    <AppBar position="sticky">
      <Toolbar>
        <IconButton
          edge="start"
          color="inherit"
          aria-label="menu"
          sx={{ mr: 2, display: { sm: 'none' } }}
          onClick={handleMenuOpen}
        >
          <MenuIcon />
        </IconButton>

        <Typography
          variant="h6"
          component="div"
          sx={{ cursor: 'pointer' }}
          onClick={() => navigate('/')}
        >
          SEO Patents
        </Typography>

        <Box
          component="form"
          onSubmit={handleSearch}
          sx={{
            position: 'relative',
            borderRadius: 1,
            backgroundColor: alpha('#fff', 0.15),
            '&:hover': { backgroundColor: alpha('#fff', 0.25) },
            marginLeft: 2,
            marginRight: 2,
            width: 'auto',
            flex: 1,
            maxWidth: 500
          }}
        >
          <Box sx={{ position: 'relative', display: 'flex', alignItems: 'center' }}>
            <InputBase
              placeholder="Search patents..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              sx={{
                color: 'inherit',
                padding: '8px 8px 8px 0',
                paddingLeft: '48px',
                width: '100%'
              }}
            />
            <IconButton
              type="submit"
              sx={{
                position: 'absolute',
                left: 0,
                padding: '8px',
                color: 'inherit'
              }}
            >
              <SearchIcon />
            </IconButton>
          </Box>
        </Box>

        <Box sx={{ display: { xs: 'none', sm: 'flex' } }}>
          {categories.map((category) => (
            <Button
              key={category.id}
              color="inherit"
              onClick={() => handleCategoryClick(category.id)}
            >
              {category.name}
            </Button>
          ))}
        </Box>

        <Menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={handleMenuClose}
        >
          {categories.map((category) => (
            <MenuItem
              key={category.id}
              onClick={() => handleCategoryClick(category.id)}
            >
              {category.name}
            </MenuItem>
          ))}
        </Menu>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;