import React from 'react';
import { useSearchParams } from 'react-router-dom';
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  CardActionArea
} from '@mui/material';
import { useNavigate } from 'react-router-dom';

// Sample data - In a real application, this would come from an API
const searchResults = {
  'page quality': [
    {
      id: 'US20230123456',
      title: 'Methods and systems for determining page quality',
      date: '2023-01-15',
      summary: 'A system for evaluating web page quality based on various on-page factors.'
    }
  ],
  'website structure': [
    {
      id: 'US20230123457',
      title: 'System for analyzing website structure',
      date: '2023-03-10',
      summary: 'Methods for analyzing and optimizing website architecture for search engines.'
    }
  ]
};

const SearchResults = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const query = searchParams.get('q') || '';

  // In a real application, this would be fetched from an API
  const results = Object.values(searchResults)
    .flat()
    .filter(patent =>
      patent.title.toLowerCase().includes(query.toLowerCase()) ||
      patent.summary.toLowerCase().includes(query.toLowerCase())
    );

  return (
    <Box>
      <Typography variant="h4" component="h1" gutterBottom>
        Search Results for "{query}"
      </Typography>

      <Grid container spacing={3}>
        {results.map((patent) => (
          <Grid item xs={12} sm={6} md={4} key={patent.id}>
            <Card>
              <CardActionArea onClick={() => navigate(`/patent/${patent.id}`)}>
                <CardContent>
                  <Typography variant="h6" gutterBottom noWrap>
                    {patent.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" gutterBottom>
                    Patent ID: {patent.id}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" gutterBottom>
                    Date: {new Date(patent.date).toLocaleDateString()}
                  </Typography>
                  <Typography variant="body2">
                    {patent.summary}
                  </Typography>
                </CardContent>
              </CardActionArea>
            </Card>
          </Grid>
        ))}
        {results.length === 0 && (
          <Grid item xs={12}>
            <Typography variant="body1" color="text.secondary" align="center">
              No patents found matching your search criteria.
            </Typography>
          </Grid>
        )}
      </Grid>
    </Box>
  );
};

export default SearchResults;